export default {
  props: {
    menuStyle: {
      type: Object,
      required: true,
    },
    menuList: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      menus: this.menuList,
    };
  },
  computed: {
  },
  methods: {
    fn() {},
    selectItem(item) {
      this.menus.forEach((menu) => {
        menu.active = false;
      });
      item.active = true;
      this.$emit('update:menu-list', this.menus);
      this.$emit('update', item);
    },
  }
};



